<template>
    <div>
        <el-button type="primary" size="small" @click="dd()">添加分类</el-button>
<el-dialog title="添加分类" :modal="false" :visible.sync="dialogFormVisible">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <table style="marginLeft:5%">
    <tr>
      <td><span>分类名称</span></td>
      <td>
        <el-input v-model="ruleForm.className" placeholder="请输入名称"></el-input>
      </td>
    </tr>
  </table>
   </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="submitForm('ruleForm')">确定</el-button>
  </div>
  </el-dialog>
    </div>
</template>
<script>
  export default {
    data() {
      return {
        dialogVisible: false,
        reload: this.reload,
        dialogTableVisible: false,
        dialogFormVisible: false,
        di:this.id,
       ruleForm:{
          className:'',
          recursionId:'0',
          aswid:'1'
        }
      };
    },
    methods: {
        dd(){
            this.dialogFormVisible =true
        },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
           this.axios.post(this.$api+"/api/ClassifyAdd",{
                className:this.ruleForm.className,
                recursionId:Number(this.ruleForm.recursionId),
                aswid:Number(this.ruleForm.aswid)
            })
            this.$message({
                    message: '添加成功',
                   type: 'success',
                })
            this.dialogVisible =false,
            location.reload()
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
    },
  };
</script>


